html {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  color: $primary;
}

main {
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  min-height: 60vh;
  @media (min-width: 960px) {
    padding-left: 100px;
    padding-right: 100px;
  }
}

h1,
h2 {
  font-weight: 400;
}

ul,
p {
  line-height: 26px;
}
